import React, { useEffect } from "react";
import { Button } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import styles from "./AddBasketModal.module.css";

const AddBasketModal = ({onOpenModal}) => {

    const navigate = useNavigate();

    const disableScroll = () => {
        document.body.style.cssText = `
        position: fixed; 
        top: -${window.scrollY}px;
        overflow-y: scroll;
        width: 100%;`;
    }

    const enableScroll = () => {
        const scrollY = document.body.style.top;
        document.body.style.cssText = '';
        window.scrollTo(0, parseInt(scrollY || '0', 10) * -1);
    }

    useEffect(() => {
        disableScroll();
        return () => enableScroll();
    }, []);

    return (
        <div className={styles["basket-modal"]}>
            
            <div className={styles["modalBox"]}>
                <h3>ì¥ë°”êµ¬ë‹ˆì— ìƒí’ˆì´ ë‹´ê²¼ìŠµë‹ˆë‹¤!</h3>
                <div className={styles["btnGroup"]}>
                    <Button className={styles["goBasketBtn"]} onClick={() => navigate('/cart')}>ì¥ë°”êµ¬ë‹ˆë¡œ</Button>
                    <Button className={styles["closeBtn"]} onClick={onOpenModal}>í™•ì¸</Button>
                </div>
            </div>
            <div className={styles["bg"]}></div>
        </div>
    )
}

export default AddBasketModal;